import React, { useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Row, Col, Image, Card, Button, ListGroup } from 'react-bootstrap'
import products from '../../seeds/products'
import Rating from '../components/Rating'
import axios from 'axios'

const ProductScreen = ({ match }) => {
    const [product, setProduct] = useState([])

    useEffect(() => {
        axios.get(`/api/products/${match.params.id}`)
            .then(res => setProduct(res.data))
            .catch(e => console.log(e))

    })
    return (
        <div>
            <Row>
                <Col md={5}>
                    <Image src={product.image} alt={product.name} fluid />
                </Col>
                <Col md={4}>
                    <ListGroup variant='flush'>
                        <ListGroup.Item>{product.name}</ListGroup.Item>
                        <ListGroup.Item>
                            Rate: <Rating rating={product.rating} text={`${product.numReviews} reviews`} />
                        </ListGroup.Item>
                        <ListGroup.Item><b>Price:</b> ${product.price}</ListGroup.Item>
                        <ListGroup.Item><b>Description:</b> {product.description}</ListGroup.Item>
                    </ListGroup>
                </Col>
                <Col md={3}>
                    <ListGroup variant="flush">
                        <Card>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Price:</Col>
                                    <Col>{product.price}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Row>
                                    <Col>Status:</Col>
                                    <Col>{product.countInStock > 0 ? 'In Stock' : "Out of Stock"}</Col>
                                </Row>
                            </ListGroup.Item>
                            <ListGroup.Item>
                                <Button className='btn btn-info btn-block'
                                    disabled={product.countInStock === 0}>Add To Cart</Button>
                            </ListGroup.Item>
                        </Card>
                    </ListGroup>
                </Col>
            </Row>

        </div>
    )
}

export default ProductScreen